import React from "react";
import icon from "./images/notepad.ico";
import "./styles/notepad.css";
import Topbar from "./Topbar";

function Notepad({ app }) {
  return (
    <div className="all_app" id="notepad_app">
      <Topbar
        app_id={"notepad_app"}
        title={"Notepad"}
        window_id={app}
        icon={icon}
      />
      <div className="notetop">
        <input type="text" id="note-name" />
        <button id="savebtn">Save</button>
        <select
          name="font-family"
          id="font-family"
          onChange={() => {
            const notebody = document.getElementById("note-body");
            const ff = document.getElementById("font-family").value;

            notebody.style.fontFamily = ff;
          }}
        >
          <option value="inherit">Default</option>
          <option value="Lucida Handwriting">Cool</option>
          <option value="Lucida Calligraphy">Caligraphy</option>
          <option value="Fira Mono">Mono</option>
        </select>
        <select
          name="fontsize"
          id="fontsize"
          onChange={() => {
            const notebody = document.getElementById("note-body");
            const fs = document.getElementById("fontsize").value;

            notebody.style.fontSize = fs;
          }}
        >
          <option value="13px">13px</option>
          <option value="15px" selected>
            15px
          </option>
          <option value="18px">18px</option>
          <option value="24px">24px</option>
        </select>
      </div>
      <textarea className="note-body" id="note-body">
        Start writing your notes
      </textarea>
    </div>
  );
}

export default Notepad;
