import React from "react";
import "./styles/topbar.css";
function Topbar({ app_id, title, window_id, icon }) {
  const maxmize = () => {
    const application = document.getElementById(app_id);
    application.style.width = `100%`;
    application.style.height = `95%`;
    application.style.marginBottom = `36px`;
    application.style.borderRadius = `0px`;
    const taskbar = document.getElementById("taskbar");
    taskbar.style.opacity = 1;
  };
  const minimize = () => {
    const application = document.getElementById(app_id);
    application.style.width = `75%`;
    application.style.height = `75%`;
    application.style.marginBottom = `0px`;
    application.style.borderRadius = `3px`;
    const taskbar = document.getElementById("taskbar");
    taskbar.style.opacity = "0.8";
  };

  const hide = () => {
    const application = document.getElementById(window_id);
    application.style.top = "120%";
  };
  return (
    <div className="topbar">
      <div
        style={{
          display: "flex",
          alignItems: "center",
          height: "100%",
          paddingLeft: "5px",
        }}
      >
        <div className="icon" style={{ backgroundImage: `url(${icon})` }}></div>
        <font id="title">{title}</font>
      </div>
      <div className="actions">
        <div>
          <button className="action" id="maximize" onClick={maxmize}></button>
        </div>
        <div>
          <button className="action" id="minimize" onClick={minimize}></button>
        </div>
        <div>
          <button className="action close" name="close" onClick={hide}></button>
        </div>
      </div>
    </div>
  );
}

export default Topbar;
